import React, { useEffect } from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import { SNAPSHOT } from 'Common/Utils';
import * as code from './StringCodes';
import N from '../../scripts';

export const Example = () => (
	<Section
		title='Example'
		context='you can choose the dropdown shadow style by float or flat.'>
		<HoverCopy code={code.Example}>
			<CodeReadOnly code={code.Example} />
			<div className='n-dropdown show for-show'>
				<button
					className='n-btn n-dropdown-toggle'
					type='button'
					aria-haspopup='true'
					aria-expanded='true'>
					Example
				</button>
				<div
					className='n-dropdown-menu'
					onClick={(e) => e.preventDefault()}
					style={{
						display: 'block',
						position: 'static',
					}}>
					<div className='n-dropdown-content'>
						<a className='n-dropdown-item' href='#'>
							Item 1
						</a>
						<a className='n-dropdown-item' href='#'>
							Item 2
						</a>
						<a className='n-dropdown-item' href='#'>
							Item 3
						</a>
						<a className='n-dropdown-item' href='#'>
							Item 4
						</a>
					</div>
				</div>
			</div>
		</HoverCopy>
	</Section>
);

export const LiveDemo = () => {
	useEffect(() => {
		if(SNAPSHOT) return;
		const instance = N.dropdown.getInstance(
			document.querySelector('#live-demo .n-dropdown')
		);
		return () => instance.destroy();
	}, []);

	return (
		<Section
			title='Live Demo'
			context='you can click the button below to see the live demo.'>
			<HoverCopy code={code.LiveDemo}>
				<CodeReadOnly code={code.LiveDemo} />
				<div className='n-dropdown'>
					<button
						className='n-btn n-dropdown-toggle'
						type='button'
						aria-haspopup='true'
						aria-expanded='false'>
						Dropdown
					</button>
					<div className='n-dropdown-menu' onClick={(e) => e.preventDefault()}>
						<div className='n-dropdown-content'>
							<a className='n-dropdown-item' href='#'>
								Item 1
							</a>
							<a className='n-dropdown-item' href='#'>
								Item 2
							</a>
							<a className='n-dropdown-item' href='#'>
								Item 3
							</a>
							<a className='n-dropdown-item' href='#'>
								Item 4
							</a>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

