export const Interactive = () => {
	useEffect(() => {
		if(SNAPSHOT) return;
		const instance = N.dropdown.getInstance(
			document.querySelector('#interactive .n-dropdown')
		);
		return () => instance.destroy();
	}, []);
	return (
		<Section
			title='Interactive'
			context={
				<>
					by putting <b>data-interactive="true"</b> in attribute. the dropdown
					menu will be interactive and will be close only when clicking the
					toggler or the background.
				</>
			}>
			<HoverCopy code={code.Interactive}>
				<CodeReadOnly code={code.Interactive} />
				<div className='n-dropdown' data-interactive>
					<button
						className='n-btn n-dropdown-toggle'
						type='button'
						aria-haspopup='true'
						aria-expanded='false'>
						Interactive
					</button>
					<div className='n-dropdown-menu' style={{ minWidth: '250px' }}>
						<div className='n-dropdown-content'>
							<div className='n-form-field'>
								<label>Username</label>
								<input
									className='n-form-control'
									type='text'
									placeholder='enter username...'
									name='username'
								/>
							</div>
							<div className='n-form-field'>
								<label>Password</label>
								<input
									className='n-form-control'
									type='password'
									placeholder='enter password...'
									name='password'
								/>
							</div>
							<div className='n-form-field'>
								<label className='n-form-check'>
									<input
										className='n-form-control'
										type='checkbox'
										name='remember'
									/>
									remember me
								</label>
							</div>
							<button className='n-btn'>Submit</button>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const DropdownTree = () => {
	useEffect(() => {
		if(SNAPSHOT) return;
		const instances = Array.from(
			document.querySelectorAll('#dropdown-tree .n-dropdown')
		).map((dropdown) => N.dropdown.getInstance(dropdown));
		return () => instances.forEach((instance) => instance.destroy());
	}, []);
	return (
		<Section title='Dropdown Tree' context='you can also achieve dropdown tree'>
			<HoverCopy code={code.DropdownTree}>
				<CodeReadOnly code={code.DropdownTree} />
				<div className='n-dropdown'>
					<button
						className='n-btn n-dropdown-toggle'
						type='button'
						aria-haspopup='true'
						aria-expanded='false'>
						Tree
					</button>
					<div className='n-dropdown-menu' onClick={(e) => e.preventDefault()}>
						<div className='n-dropdown-content'>
							<a className='n-dropdown-item' href='#'>
								Item 1
							</a>
							<a className='n-dropdown-item' href='#'>
								Item 2
							</a>
							<div className='n-dropdown'>
								<a className='n-dropdown-item n-dropdown-toggle' href='#'>
									Item 3 ❱
								</a>
								<div className='n-dropdown-menu'>
									<div className='n-dropdown-content'>
										<a className='n-dropdown-item' href='#'>
											Item 3 1
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 3 2
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 3 3
										</a>
										<div className='n-dropdown'>
											<a className='n-dropdown-item n-dropdown-toggle' href='#'>
												Item 3 4 ❱
											</a>
											<div className='n-dropdown-menu'>
												<div className='n-dropdown-content'>
													<a className='n-dropdown-item' href='#'>
														Item 3 4 1
													</a>
													<a className='n-dropdown-item' href='#'>
														Item 3 4 2
													</a>
													<a className='n-dropdown-item' href='#'>
														Item 3 4 3
													</a>
													<a className='n-dropdown-item' href='#'>
														Item 3 4 4
													</a>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div className='n-dropdown'>
								<a className='n-dropdown-item n-dropdown-toggle' href='#'>
									Item 4 ❱
								</a>
								<div className='n-dropdown-menu'>
									<div className='n-dropdown-content'>
										<a className='n-dropdown-item' href='#'>
											Item 4 1
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 4 2
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 4 3
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 4 4
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 4 4
										</a>
										<a className='n-dropdown-item' href='#'>
											Item 4 4
										</a>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const Javascript = () => (
	<Section
		title='Javascript'
		context={
			<>
				using <b>N.dropdown.getInstance(dropdownElement)</b> it can initialize
				the <b>.n-dropdown</b> when it is not initialize yet and get the
				instance at the same time. if the <b>.n-dropdown</b> is already
				initialize, it will only get the instance.
			</>
		}>
		<HoverCopy code={code.Javascript}>
			<CodeReadOnly code={code.Javascript} />
		</HoverCopy>
	</Section>
);

export const Events = () => (
	<Section>
		<HoverCopy code={code.Events}>
			<CodeReadOnly code={code.Events} />
		</HoverCopy>
	</Section>
);
